<template>
  <div class="shopbag">
    <van-nav-bar title="购物袋" left-text="返回" left-arrow :fixed="true" @click-right="edit" @click-left="goback">
      <template #right v-if="shopbagData.length > 0">
        <div>{{isEdit?'完成':"编辑"}}</div>
      </template>
    </van-nav-bar>
    <div class="presentation">
      <van-notice-bar
  left-icon="volume-o"
  :scrollable='false'
  background="#fafafa"
  color="#5f56ff"
  text="为保证你的订单和权益等信息同步，请绑定手机号"
/>
    </div>
    <div v-if="shopbagData.length > 0">
      <van-swipe-cell :disabled="!isEdit" v-for="(item, index) in shopbagData" :key="index">
        <van-cell :border="false">
          <div class="clearfix cell-box">
            <div class="fl check">
              <van-checkbox
                v-model="item.isCheck"
                icon-size="24px"
                checked-color="#5f56ff"
                @change="selectone"
              ></van-checkbox>
            </div>
            <div class="fl pro-img">
              <img class="auto-img" :src="item.small_img" />
            </div>
            <div class="fl pro-info">
              <div class="text-box">
                <div class="clearfix">
                  <div class="fl text-name">{{item.name}}</div>
                  <div class="fl text-rule">{{item.rule}}</div>
                </div>
                <div class="text-enname">{{item.enname}}</div>
              </div>
              <div class="price-box">
                <div class="fl price">￥{{item.price}}</div>
                <div class="fr">
                  <van-stepper
                    v-model="item.count"
                    theme="round"
                    button-size="24"
                    disable-input
                    @change="modifycount(item)"
                  />
                </div>
              </div>
            </div>
          </div>
        </van-cell>

        <template #right>
          <van-button square type="danger" text="删除" @click="removeoneShopbagdata(item.sid)" color="#5f56ff" />
        </template>
      </van-swipe-cell>
    </div>

    <van-submit-bar v-show="!isEdit" :price="total"   button-text="提交订单" button-color="#5f56ff" @submit="submitorder">
      <van-checkbox v-model="allCheck" checked-color="#5f56ff" @click="selectall">全选</van-checkbox>
    </van-submit-bar>
    <van-submit-bar v-show="isEdit" button-text="删除选择" @submit="removemoreShopbagdata" button-color="#5f56ff">
      <van-checkbox v-model="allCheck" icon-size="24px" checked-color="#5f56ff">全选</van-checkbox>
    </van-submit-bar>

    <div v-if="shopbagData.length == 0">
      <van-empty description="购物袋空空如也,去逛一逛!">
        <van-button round color="#5f56ff" class="bottom-button" @click="goMenu">去逛一逛</van-button>
      </van-empty>
    </div>
  </div>
</template>

<script>
import "../../assets/less/shopbag.less";
import { utils } from "../../assets/js/utils";
export default {
  data() {
    return {
      isEdit: false,
      allCheck: false,
      shopbagData: "",
      total:0,
    };
  },
  created() {
    //查询购物袋数据
    this.findshopbagdate();
  },
  methods: {
    // 回退
    goback(){
      this.$router.back(-1)
    },
    //回菜单页面
    goMenu() {
      this.$router.push({ name: "Menu" });
    },
    // 编辑/完成
    edit() {
      this.isEdit = !this.isEdit;
    },
    //查询购物袋数据
    findshopbagdate() {
      //获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/findAllShopcart",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();
          

          if (result.data.code == 5000) {
            result.data.result.map((v) => {
              v.isCheck = false;
            });

            this.shopbagData = result.data.result;
          } else {
            this.$toast(result.data.msg);
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    //全选
    selectall() {
      this.shopbagData.map((v) => {
        v.isCheck = this.allCheck;
      });
      this.sum();
    },

    //单选
    selectone() {
      this.sum();
      for (let i = 0; i < this.shopbagData.length; i++) {
        if (!this.shopbagData[i].isCheck) {
          this.allCheck = false;
          return;
        }
      }

      this.allCheck = true;
    },

    //修改商品数量
    modifycount(item) {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        sid: item.sid,
        count: item.count,
      });

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "POST",
        url: "/modifyShopcartCount",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          
          this.sum();
          if (result.data.code !== 6000) {
            this.$toast(result.data.msg);
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    //删除购物袋数据
    removeshopbagdata(sids) {
      //获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      //参数序列化
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        sids: JSON.stringify(sids),
      });

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      return this.axios({
        method: "POST",
        url: "/deleteShopcart",
        data,
      });
    },

    //单个删除购物袋
    removeoneShopbagdata(sid, index) {
      this.removeshopbagdata([sid])
        .then((result) => {
          this.$toast.clear();
          
          if (result.data.code == 7000) {
            this.shopbagData.splice(index, 1);
          }

          this.$toast(result.data.msg);

          this.sum();
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
    //删除多个购物袋
    removemoreShopbagdata() {
        let sids = [];
        this.shopbagData.map(v => {
          if (v.isCheck) {
            sids.push(v.sid);
          }
        })

        this.removeshopbagdata(sids).then(result => {
          this.$toast.clear();
          
          if (result.data.code == 7000) {
            
            for (let i = this.shopbagData.length - 1; i >= 0; i--) {
              if (this.shopbagData[i].isCheck) {
                this.shopbagData.splice(i, 1);
              }
            }

            this.sum();

          }
          
          this.$toast(result.data.msg);
        }).catch(err => {
          this.$toast.clear();
          
        })
      },
  
    //计算总金额
      sum() {
        this.total = 0;
        this.shopbagData.map(v => {
          if (v.isCheck) {
            this.total += v.count * v.price;
          }
        })

        this.total *= 100;
      },

      //提交订单
      submitorder(){
        let sids = [];
        this.shopbagData.map(v => {
          if (v.isCheck) {
            sids.push(v.sid);
          }
        })

        if (sids.length === 0) {
          this.$toast('请选择购买商品');
          return;
        }

        sids = sids.join('-');

        this.$router.push({name: 'Pay', query: {sids}});
      }
  },
};
</script>

<style lang="less" scoped>
</style>